'use strict';

function sum(a, b) {
  return a + b;
};

sum(1, 2);

const burger = document.querySelector('.burger');

burger.addEventListener('click', burgerToggle);

function burgerToggle() {
  const arrow = document.querySelector('.burger__arrow');
  const nav = document.querySelector('.nav__mobile');

  arrow.classList.toggle('burger__arrow--open');
  nav.classList.toggle('nav__mobile--open');
};

$('.carousel__list').slick({
  dots: false,
  infinite: true,
  // autoplay: true,
  speed: 300,
  slidesToShow: 2,
  slidesToScroll: 2,
  // adaptiveHeight: true,
  prevArrow: $('.carousel__btn-prev'),
  nextArrow: $('.carousel__btn-next'),
  responsive: [
    {
      breakpoint: 1490,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        infinite: true,
      },
    },
  ],
});

